html {
  --nav-link-color: #00acdc;
  --header-height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  position: relative;
}

.nav__list > :first-child {
  margin-left: 0;
}

.nav__link {
  height: var(--header-height);
  line-height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  flex-direction: column;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--nav-link-color);
}

.is-active:after {
  content: "";
  background-color: var(--nav-link-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
